@import "../../styles/theme";

.experience {
  padding: 50px 0;
}

.timeline-wrapper {
  /* The actual timeline (the vertical ruler) */
  .timeline {
    position: relative;
    max-width: 1200px;
    margin: 0 auto;
  }

  /* The actual timeline (the vertical ruler) */
  .timeline::after {
    content: "";
    position: absolute;
    width: 6px;
    background-color: var(--color-grey);
    top: 0;
    bottom: 0;
    left: 50%;
    margin-left: -3px;
  }

  /* Container around content */
  .timeline-container {
    padding: 10px 40px;
    position: relative;
    background-color: inherit;
    width: 50%;
    h3 {
      font-size: 20px;
    }
    h4 {
      font-size: 18px;
      span {
        font-size: 12px;
        font-style: italic;
      }
    }
    p {
      font-size: 14px;
    }
  }

  /* The circles on the timeline */
  .timeline-container::after {
    content: "";
    position: absolute;
    width: 25px;
    height: 25px;
    right: -12px;
    background-color: var(--color-z-black);
    border: 4px solid var(--color-grey);
    top: 15px;
    border-radius: 50%;
    z-index: 1;
  }

  /* Place the timeline-container to the left */
  .left {
    left: 0;
    text-align: right;
  }

  /* Place the timeline-container to the right */
  .right {
    left: 50%;
    text-align: left;
  }

  /* Add arrows to the left timeline-container (pointing right) */
  .left::before {
    content: " ";
    height: 0;
    position: absolute;
    top: 22px;
    width: 0;
    z-index: 1;
    right: 30px;
    border: medium solid var(--color-grey);
    border-width: 10px 0 10px 10px;
    border-color: transparent transparent transparent var(--color-grey);
  }

  /* Add arrows to the right timeline-container (pointing left) */
  .right::before {
    content: " ";
    height: 0;
    position: absolute;
    top: 22px;
    width: 0;
    z-index: 1;
    left: 30px;
    border: medium solid var(--color-grey);
    border-width: 10px 10px 10px 0;
    border-color: transparent var(--color-grey) transparent transparent;
  }

  /* Fix the circle for timeline-containers on the right side */
  .right::after {
    left: -13px;
  }

  /* The actual content */
  .timeline-content {
    padding: 20px;
    background-color: var(--color-grey);
    position: relative;
    border-radius: 0px;
  }

  /* Media queries - Responsive timeline on screens less than 600px wide */
  @media screen and (max-width: 600px) {
    /* Place the timelime to the left */
    .timeline::after {
      left: 31px;
    }

    /* Full-width timeline-containers */
    .timeline-container {
      width: 100%;
      padding-left: 70px;
      padding-right: 25px;
    }

    /* Make sure that all arrows are pointing leftwards */
    .timeline-container::before {
      left: 60px;
      border: medium solid white;
      border-width: 10px 10px 10px 0;
      border-color: transparent white transparent transparent;
    }

    /* Make sure all circles are at the same spot */
    .left::after,
    .right::after {
      left: 19px;
    }

    /* Make all right timeline-containers behave like the left ones */
    .right {
      left: 0%;
    }
  }
}
